import React, { Component } from 'react';
import { v4 as uuid } from 'uuid';

import s from './App.module.css';

import Container from './components/Container';
import Filter from './components/Filter';
import AddContactForm from './components/AddContactForm';
import ContactsList from './components/ContactsList';

class App extends Component {
  state = {
    contacts: [],
    filter: '',
  };
  componentDidMount() {
    const storagedTodos = localStorage.getItem('userContacts');
    if (storagedTodos) {
      this.setState({ contacts: JSON.parse(storagedTodos) });
    }
  }
  componentDidUpdate(prevProps, prevState) {
    if (prevState.contacts !== this.state.contacts) {
      localStorage.setItem('userContacts', JSON.stringify(this.state.contacts));
    }
  }
  onAddContact = ({ name, number }) => {
    const contact = {
      id: uuid(),
      name: name,
      number: number,
    };
    const existElement = this.state.contacts.find(el => el.name === name);
    existElement
      ? alert(`${name} has already exist`)
      : this.setState(prevState => ({
          contacts: [...prevState.contacts, contact],
        }));
  };

  onChangeFilter = e => {
    this.setState({ filter: e.currentTarget.value });
  };

  onDeleteButton = id => {
    this.setState(prevState => ({
      contacts: prevState.contacts.filter(el => el.id !== id),
    }));
  };

  getVisibleContacts = () => {
    const normalizedFilter = this.state.filter.toLowerCase();
    return this.state.contacts.filter(contact =>
      contact.name.toLowerCase().includes(normalizedFilter),
    );
  };

  render() {
    const visibleContacts = this.getVisibleContacts();
    return (
      <Container>
        <h1 className={s.mainHeading}>Phonebook</h1>
        <AddContactForm onAddContact={this.onAddContact} />
        <h2>Contacts: </h2>
        <Filter onChangeFilter={this.onChangeFilter} />
        <ContactsList
          visibleContacts={visibleContacts}
          onDeleteButton={this.onDeleteButton}
        />
      </Container>
    );
  }
}

export default App;
